<template>
  <v-container>
    <v-row>
      <v-col cols="6">
        <v-btn
          width="100%"
          height="100%"
          min-height="200px"
          @click.stop="addItem()"
          ><v-icon left> mdi-plus </v-icon>Add</v-btn
        >
      </v-col>

      <v-col
        v-for="item in sortedList"
        :key="item.id"
        class="d-flex child-flex"
        cols="6"
      >
        <ItemCard
          :is-tile="true"
          :show-metadata="showMetadata"
          :image-src="item.avatar"
          @select-item="selectItem(item.id)"
          header-class="align-end"
        >
          <template v-slot:title>
            {{ item.name }}
          </template>

          <template v-slot:metadata>
            <div>
              {{ item.description }}
            </div>
            <div>
              {{ item.dateCreated }}
            </div>
          </template>
        </ItemCard>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import ItemCard from "@/components/ItemCard.vue";

export default {
  components: {
    ItemCard,
  },
  data() {
    return {};
  },

  mounted() {},

  // cant edit data in computeds, use watch instead
  computed: {
    ...mapGetters({
      catalog: "getCatalog",
      userPrefs: "getPrefs",
    }),
    showMetadata() {
      return this.userPrefs.showMetadata;
    },
    altSortToggle() {
      return this.userPrefs.sortOrder;
    },
    sortedList() {
      const toggled = this.altSortToggle;
      console.log(`Catalog.sortedList toggled: ${toggled ? "123" : "abc"}`);
      // return toggled ? this.nameSortedList : this.dateSortedList;
      return toggled ? this.dateSortedList : this.nameSortedList;
    },
    nameSortedList() {
      return this.sortList("name");
    },
    dateSortedList() {
      return this.sortList("dateCreated");
    },
  },

  watch: {
    showMetadata(val) {
      console.log(`Catalog:watch:showMetadata: ${val}`);
    },
    altSortToggle(val) {
      console.log(`Catalog:watch:altSortToggle: ${val ? "123" : "abc"}`);
    },
  },

  methods: {
    ...mapActions({
      addNewItem: "addItem",
    }),
    sortList(key) {
      return [...this.catalog].sort((a, b) => {
        const aKey = a[key].toLowerCase();
        const bKey = b[key].toLowerCase();
        // console.log(`keys: ${aKey}, ${bKey}`);
        if (aKey < bKey) return -1;
        if (aKey > bKey) return 1;
        return 0;
      });
    },
    addItem() {
      console.log(`addItem`);
      // this.showDialog = true;
      this.$router.push({ name: "items.add" });
    },
    selectItem(id) {
      console.log(`Catalog.selectItem: ${id}`);
      this.$router.push({ name: "Item", params: { slug: id } });
    },
  },
};
</script>