import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import db from './qd'
import userPrefsModule from './modules/userPrefs'
import actionDecorationsModule from './modules/actionDecorations'

Vue.use(Vuex);

export default new Vuex.Store({
  // strict: true, // dev only - ensures im not changing state incorrectly
  modules: {
    userPrefs: userPrefsModule,
    actionDecorations: actionDecorationsModule,
  },
  // plugins: [createPersistedState({ paths: ['sortOrder', 'selectedKeys', 'showMetadata'] })],
  // plugins: [createPersistedState()],
  plugins: [createPersistedState({ paths: ['userPrefs', 'items', 'attrs', 'assets', 'fakeId'] })],
  state: {
    items: db.items,
    attrs: db.attrs,
    assets: db.assets,
    albums: db.albums,
    isEditing: false,
    // TODO: refactor the way saving is wired
    isSaving: false,
    isDeleting: false,
    fakeId: 10000,
    // https://github.com/vuejs/vuex/issues/24
  },
  mutations: {
    SET_EDITING_FLAG: (state, flag) => {
      console.log(`SET_EDITING_FLAG: ${flag}`)
      state.isEditing = flag;
    },
    SET_SAVING_FLAG: (state, flag) => {
      console.log(`SET_SAVING_FLAG: ${flag}`)
      state.isSaving = flag;
    },
    SET_DELETING_FLAG: (state, flag) => {
      console.log(`SET_DELETING_FLAG: ${flag}`)
      state.isDeleting = flag;
    },
    ADD_NEW_ITEM: (state, item) => {
      console.log(`ADD_NEW_ITEM`)
      const id = state.fakeId++
      state.items.unshift({
        ...item,
        id: id.toString(),
        dateCreated: `2010.01.${id}`
      });
    },
    UPDATE_ITEM: (state, item) => {
      console.log(`UPDATE_ITEM`)
      const index = state.items.findIndex(i => i.id === item.id);
      // if (index) state.items[index] = item;
      if (index >= 0) state.items.splice(index, 1, item);
    },
    DELETE_ITEM: (state, item) => {
      console.log(`DELETE_ITEM`)
      const index = state.items.findIndex(i => i.id === item.id);
      // if (index) state.items[index] = item;
      if (index >= 0) state.items.splice(index, 1);
    },
    ADD_NEW_ATTRIBUTES: (state, attributes) => {
      console.log(`ADD_NEW_ATTRIBUTES`)
      attributes = attributes.map(attr => {
        const id = state.fakeId++;
        return {
          title: attr.title,
          id: id.toString()
        }
      })
      state.attrs = [...state.attrs, ...attributes]
    },
  },
  actions: {
    deleteItem: ({ commit }, item) => {
      commit('DELETE_ITEM', item)
      commit(`SET_DELETING_FLAG`, false)
      commit(`SET_EDITING_FLAG`, false)
    },
    addItem: ({ commit }, item) => {
      // console.log(`addItem: ${item}`);
      // just the ids
      const cleanItem = {
        ...item,
        name: item.name || '',
        attributes: item.attributes.map(a => a.id),
        assets: item.assets.map(a => a.id),
        avatar: item.avatar?.id || item.avatar,
      }
      commit('ADD_NEW_ITEM', cleanItem)
      commit(`SET_SAVING_FLAG`, false)
      commit(`SET_EDITING_FLAG`, false)
    },
    updateItem: ({ commit }, item) => {
      // console.log(`addItem: ${item}`);
      // just the ids
      const cleanItem = {
        ...item,
        name: item.name || '',
        attributes: item.attributes.map(a => a.id),
        assets: item.assets.map(a => a.id),
        avatar: item.avatar?.id || item.avatar,
      }
      commit('UPDATE_ITEM', cleanItem)
      commit(`SET_SAVING_FLAG`, false)
      commit(`SET_EDITING_FLAG`, false)
    },
    addAttributes: ({ commit }, attributes) => {
      // console.log(`addAttributes: ${attributes}`);
      commit('ADD_NEW_ATTRIBUTES', attributes)
    },
    setEditingFlag: ({ commit }, flag) => {
      commit('SET_EDITING_FLAG', flag)
    },
    setSavingFlag: ({ commit }, flag) => {
      commit('SET_SAVING_FLAG', flag)
    },
    setDeletingFlag: ({ commit }, flag) => {
      commit('SET_DELETING_FLAG', flag)
    },
  },
  getters: {
    // gets a fully populated tree
    getCatalog: (state, getters) => {
      console.log(`store:getters:getCatalog`)
      return state.items.map(i => {
        return {
          ...i,
          assets: i.assets.map(id => getters.getAsset(id)) || i.assets,
          avatar: getters.getAsset(i.avatar)?.src || i.avatar,
          // heavy handed for first hit (not DRY), but try to test mobile
          attributes: i.attributes.map(id => getters.getAttribute(id)) || i.attributes,
        }
      })
    },
    getExportData: (state, getters) => {
      const exportData = {
        attrs: state.attrs,
        items: state.items,
        assets: state.assets,
        catalog: getters.getCatalog
      }
      return JSON.stringify(exportData, null, 2)
    },
    getAlbums: (state) => {
      return state.albums.map(a => {
        return {
          id: a.id,
          title: a.title,
        }
      })
    },
    getAlbum: (state) => (id) => {
      console.log(`store:getters:getAlbum - id: ${id}`)
      return state.albums.find(a => a.id === id)
    },
    sortedKeyList: (state, getters) => {
      // TODO: refactor? im getting the set from a map that takes time to build information not needed for this use case
      console.time("get sorted key list from key map");
      let sortedKeyList = Object.keys(getters.itemsByKey)
        .sort(
          (a, b) => {
            const A = a.toUpperCase();
            const B = b.toUpperCase();
            if (A < B) return -1;
            if (A > B) return 1;
            return 0;
          }
        )
        .map((keyString) => {
          let foundItem = getters.getCatalog.find(
            (item) => item.name === keyString
          );
          if (foundItem) {
            return { name: keyString, avatar: foundItem.avatar };
          }
          return { name: keyString, icon: "label" };
        });
      console.timeEnd("get sorted key list from key map");
      return sortedKeyList;
    },
    sortedAttributes: (state) => {
      return state.attrs.sort(
        (a, b) => {
          const A = a.title.toUpperCase();
          const B = b.title.toUpperCase();
          if (A < B) return -1;
          if (A > B) return 1;
          return 0;
        }
      );
    },
    // TODO:is this reactive?
    itemsByKey: (state, getters) => {
      const keyItemsMap = {};
      console.time("get a map of key to items");
      getters.getCatalog.forEach((item) => {
        let itemsInKey = keyItemsMap[item.name];
        if (itemsInKey) {
          itemsInKey.push(item);
        } else {
          keyItemsMap[item.name] = [item];
        }
        item.attributes.forEach((attr) => {
          let itemsInKey = keyItemsMap[attr.title];
          if (itemsInKey) {
            itemsInKey.push(item);
          } else {
            keyItemsMap[attr.title] = [item];
          }
        });
      });
      console.timeEnd("get a map of key to items");
      return keyItemsMap;
    },
    // is this reactive?
    itemsWithKey: (state, getters) => (key) => {
      console.log(`store:getters:itemsWithKey: ${key}`)
      return getters.itemsByKey[key];
    },
    // gets a fully populated item
    getItem: (state, getters) => (id) => {
      // console.log(`store:getters:getItem '${id}'`)
      if (!id) return
      return getters.getCatalog.find(item => {
        // console.log(`is attributes objects or ids? ${JSON.stringify(item.attributes, null, 2)}`)
        return item.id === id;
      });
    },
    getPopulatedItem: (state, getters) => (id) => {
      // console.log(`store:getters:getItem '${id}'`)
      if (!id) return
      const sparceItem = state.items.find(item => {
        // console.log(`is attributes objects or ids? ${JSON.stringify(item.attributes, null, 2)}`)
        return item.id === id;
      })
      return {
        ...sparceItem,
        assets: sparceItem.assets.map(id => getters.getAsset(id)) || sparceItem.assets,
        avatar: getters.getAsset(sparceItem.avatar) || sparceItem.avatar,
        // heavy handed for first hit (not DRY), but try to test mobile
        attributes: sparceItem.attributes.map(id => getters.getAttribute(id)) || sparceItem.attributes,
      }
    },
    getAsset: (state) => (id) => {
      // console.log(`store:getAsset '${id}'`)
      const asset = state.assets.find(asset => {
        // console.log(`store:getAsset '${id}' found!`)
        return asset.id === id;
      })
      if (asset) return asset
      return state.albums[0].images.find(asset => {
        // console.log(`store:getAsset '${id}' found!`)
        return asset.id === id;
      })
    },
    getAttribute: (state) => (id) => {
      // console.log(`store:getters:getAttr '${id}'`)
      return state.attrs.find(attr => {
        // console.log(`store:getters:getAttr '${id}' found!`)
        return attr.id === id;
      })
    },
    getAttributes: (state) => (ids) => {
      // console.log(`store:getters:getAttrs '${ids}'`)
      return state.attrs.filter(attr => {
        console.log(`store:getters:getAtters '${ids}' found!`)
        return ids.includes(attr.id);
      })
    },
    getAttributesByTitle: (state) => (titles) => {
      return state.attrs.filter(attr => {
        return titles.includes(attr.title)
      })
    },
    getNewestItemId: (state) => {
      return state.items[0].id
    }

  },
})
