<template>
  <v-app id="app">
    <v-dialog v-model="showConfirmDelete">
      <v-card>
        <v-card-title> You sure? </v-card-title>
        <v-card-text>This action cannot be undone.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="showConfirmDelete = false"> Cancel </v-btn>
          <v-btn text @click="deleteItem"> Delete </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-app-bar hide-on-scroll :input-value.sync="showNavs" app>
      <v-app-bar-nav-icon
        @click.stop="toggleNavDrawer = !toggleNavDrawer"
      ></v-app-bar-nav-icon>
      <v-toolbar-title class="text-capitalize">{{ title }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-checkbox
        v-for="toggle in contextToggles(context)"
        :key="toggle.toggle"
        v-model="toggleStates[toggle.toggle]"
        :off-icon="toggle.offIcon"
        :on-icon="toggle.onIcon"
      >
      </v-checkbox>
    </v-app-bar>
    <v-navigation-drawer v-model="toggleNavDrawer" fixed temporary>
      <v-list dense nav>
        <v-list-item
          v-for="action in navDrawerActions"
          :key="action.title"
          :to="action.route"
        >
          <v-list-item-icon>
            <v-icon>mdi-{{ action.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="text-capitalize">
              {{ action.title }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-main>
      <intersect @enter="hasScrolled = false" @leave="hasScrolled = true">
        <div id="pixel-to-watch"></div>
      </intersect>
      <router-view />
    </v-main>

    <!-- TODO: save blows this up - this should be in specific routes so contextual actions have contextual data -->
    <v-bottom-navigation hide-on-scroll :input-value.sync="showNavs" grow app>
      <v-btn
        v-for="action in bottomNavActions(context)"
        :key="action.title"
        @click.stop="!!action.click && action.click()"
        :to="action.route"
        :input-value="isActiveNav(action.title)"
        :show="action.isVisible"
      >
        <span class="text-capitalize">{{ action.title }}</span>
        <v-icon>mdi-{{ action.icon }}</v-icon>
      </v-btn>
    </v-bottom-navigation>
  </v-app>
</template>
<script>
import { mapState, mapGetters, mapActions } from "vuex";
import Intersect from "vue-intersect";
export default {
  components: {
    Intersect,
  },
  data: () => ({
    title: "welcome to the demo",
    toggleNavDrawer: false,
    showNavs: true,
    showConfirmDelete: false,
    toggleStates: {
      showMetadata: true,
      altSortToggle: true,
    },
    // isEditing: false,
    hasScrolled: false,
  }),

  beforeMount() {
    this.toggleStates.showMetadata = this.getPrefs.showMetadata;
    this.toggleStates.altSortToggle = this.getPrefs.sortOrder;
  },

  computed: {
    ...mapGetters(["getPrefs", "getBottomNavActions"]),
    ...mapGetters({
      navDrawerActions: "getNavDrawerActions",
      contextToggles: "getContextToggles",
    }),
    ...mapState(["isEditing", "isSaving", "isDeleting"]),
    context() {
      return this.$route.name?.toLowerCase() || null;
    },
  },

  methods: {
    ...mapActions([
      "setShowMetadata",
      "setSortOrder",
      "setEditingFlag",
      "setSavingFlag",
      "setDeletingFlag",
    ]),
    bottomNavActions(context) {
      return this.getBottomNavActions(context)
        .filter((action) => {
          return this.isVisible(action.title);
        })
        .map((action) => {
          return {
            ...action,
            click: this[action.click],
          };
        });
    },
    isVisible(action) {
      const whitelist = ["top", "edit", "save", "cancel", "delete"];
      if (!whitelist.includes(action)) {
        return true;
      }
      if (action === "top") {
        return this.hasScrolled;
      }
      if (this.isEditing) {
        return (
          // action === "save" || (action === "cancel") | (action === "delete")
          action === "save" || action === "cancel" || action === "delete"
        );
      } else {
        return action === "edit";
      }
    },
    isActiveNav(key) {
      return key === this.$route.name?.toLowerCase() || false;
    },
    toTop() {
      console.log(`App.toTop`);
      this.$vuetify.goTo(0);
    },
    toPrev() {
      console.log(`App.toPrev`);
      this.$router.go(-1);
    },
    save() {
      console.log(`App.save`);
      // TODO: ugg - how to get the item from router-view to this?
      // this.isEditing = false;
      this.setSavingFlag(true);
      this.setEditingFlag(false);
    },
    edit() {
      // console.log(`edit`);
      // this.isEditing = true;
      this.setEditingFlag(true);
    },
    cancel() {
      // console.log(`cancel`);
      // this.isEditing = false;
      this.setEditingFlag(false);
    },
    delete() {
      console.log(`App.delete popping confirm dialog`);
      this.showConfirmDelete = true;
    },
    deleteItem() {
      console.log(`App.delete from catalog`);
      this.setDeletingFlag(true);
      this.setEditingFlag(false);
      this.showConfirmDelete = false;
      // TODO: route change here didnt delete - probably race condition
    },
    setTitle(itemSlug, toName) {
      console.log(`App.setTitle`);
      if (itemSlug === "add" || toName === "items.add") {
        this.title = "add item";
      } else if (itemSlug || toName) {
        this.title = this.$store.getters.getItem(itemSlug)?.name || toName;
      }
    },
  },

  watch: {
    isSaving(val) {
      // i dont think this is async, but this is lagging by 1 update...
      console.log(`App:watch:isSaving changed: ${val}`);
      if (this.$route.params.slug !== "add")
        this.setTitle(this.$route.params.slug);
    },
    // hasScrolled(val) {
    //   console.log(`hasScrolled: ${val}`);
    // },
    toggleStates: {
      handler() {
        // console.log("toggle state changed!", newValue);
        this.showNavs = true;
      },
      deep: true,
    },
    "toggleStates.showMetadata": function (newVal) {
      // "toggleState.showMetadata": (newVal, oldVal) => {
      // console.log(`watch:showMetadata { old: ${oldVal}, new: ${newVal} }`);
      // this.toggleShowMetadata();
      this.setShowMetadata(newVal);
    },
    "toggleStates.altSortToggle": function (newVal) {
      // console.log(`watch:altSortToggle { old: ${oldVal}, new: ${newVal} }`);
      // this.toggleSortOrder();
      this.setSortOrder(newVal);
    },
    getPrefs() {
      this.toggleStates.showMetadata = this.getPrefs.showMetadata;
      this.toggleStates.altSortToggle = this.getPrefs.sortOrder;
    },
    $route(to) {
      // react to route changes...
      console.log(`App:watch:route:to: ${to.name}`);
      this.setTitle(to.params.slug, to.name);
      this.showNavs = true;
      this.setSavingFlag(false);
      this.setEditingFlag(false);
    },
  },
};
</script>
<style>
#pixel-to-watch {
  width: 1px;
  height: 1px;
  /* width: 100px; */
  /* height: 100px; */
  /* background-color: black; */
  /* box-shadow: inset 0 0 0 4px green; */
}
