export default {
  state: () => ({
    toggles: {
      showMetadata: {
        toggle: "showMetadata",
        onIcon: "mdi-information",
        offIcon: "mdi-information-outline",
      },
      altSortToggle: {
        toggle: "altSortToggle",
        onIcon: "mdi-sort-numeric-variant",
        offIcon: "mdi-sort-alphabetical-variant",
      },
      darkTheme: {
        toggle: "darkTheme",
        onIcon: "mdi-theme-light-dark",
        offIcon: "mdi-theme-light-dark",
      },
    },
    // TODO: routes are too coupled with route index page
    routeActions: {
      home: {
        title: "home",
        icon: "home",
        route: "/",
      },
      about: {
        title: "about",
        icon: "information",
        route: "/about",
      },
      search: {
        title: "search",
        icon: "magnify",
        route: "/search",
      },
      catalog: {
        title: "catalog",
        icon: "library-shelves",
        route: "/catalog",
      },
      settings: {
        title: "settings",
        icon: "cog",
        route: "/settings",
      },
      test: {
        title: "test",
        icon: "alert-decagram",
        route: "/test",
      },
    },
    // TODO: clicks are too coupled with app page
    contextActions: {
      back: { title: "back", click: "toPrev", icon: "arrow-left" },
      top: { title: "top", click: "toTop", icon: "arrow-up" },
      edit: { title: "edit", click: "edit", icon: "pencil-outline" },
      save: { title: "save", click: "save", icon: "check" },
      delete: { title: "delete", click: "delete", icon: "delete" },
      cancel: { title: "cancel", click: "cancel", icon: "close" },
    },
  }),
  mutations: {},
  actions: {},
  getters: {
    getContextToggles: (state) => (context) => {
      console.log(`store:getToggles '${context}'`)
      const whitelist = ['catalog', 'search', 'item', 'settings']
      if (!whitelist.includes(context)) {
        return [];
      }
      const contextToggleMap = {
        default: [
          state.toggles.showMetadata,
          state.toggles.altSortToggle,
        ],
        search: [
          state.toggles.showMetadata,
        ],
        item: [
          state.toggles.showMetadata,
        ],
      }
      return contextToggleMap[context] || contextToggleMap.default;
    },
    getNavDrawerActions: (state) => {
      // console.log(`store:getContextActions '${context}'`)
      const actions = state.routeActions;
      return [
        actions.home,
        actions.about,
        actions.catalog,
        actions.search,
        actions.settings,
        actions.test,
      ]
    },
    getBottomNavActions: (state) => (context) => {
      console.log(`store:getContextActions '${context}'`)
      const whitelist = ['catalog', 'search', 'item', 'settings', 'items.add'];
      if (!whitelist.includes(context)) {
        return [];
      }
      const bottomNavActionsMap = {
        default: [
          state.routeActions.catalog,
          state.contextActions.top,
          state.routeActions.search,
          state.routeActions.settings,
        ],
        item: [
          state.contextActions.back,
          state.contextActions.top,
          state.contextActions.edit,
          state.contextActions.save,
          state.contextActions.cancel,
          state.contextActions.delete,
        ],
        'items.add': [
          state.contextActions.back,
          state.contextActions.top,
          state.contextActions.edit,
          state.contextActions.save,
          state.contextActions.cancel,
        ],
      }
      return bottomNavActionsMap[context] || bottomNavActionsMap.default
    },
  }
};